import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { PeriodoModel } from 'src/app/commons/models/evaluacion-docente/mantenedores/periodos.model';
import { MantenedoresEvaluacionDocenteService } from 'src/app/commons/services/evaluacion-docente-services/mantenedores.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';

@Component({
  selector: 'app-mantenedor-periodos',
  templateUrl: './mantenedor-periodos.component.html',
  styles: [
  ]
})
export class MantenedorPeriodosComponent implements OnInit {

  public listadoPeriodos: PeriodoModel[] = [];

  public isModoEdicion: boolean = false;
  public isEdit: boolean = false;

  public formPeriodo: FormGroup;

  //** PAGINATION PREGUNTAS */
  public searchPagination: SearchPagination<string> = null;
  public pageSize: number = 10;
  public page: number = 1;
  public totalElements = 0;
  public buscador: string = '';

  public formTopBar: FormGroup;
  //** */

  public loading = false;
  @BlockUI('mantenedorPeriodosEvaluacionDocente') blockUI: NgBlockUI;

  constructor(
    private mantenedorService: MantenedoresEvaluacionDocenteService,
    private toastr: ToastrService,
    private utilAlert: UtilAlert
  ) { }

  ngOnInit(): void {
    this.initFormTopBar();
    this.listarPeriodos();
  }

  initFormTopBar() {
    this.formTopBar = new FormGroup({
      buscador: new FormControl(""),
      registros: new FormControl("")
    })

    this.formTopBar.get("buscador").setValue(this.buscador);
    this.formTopBar.get("registros").setValue(this.pageSize.toString());
  }

  initFormPeriodo(periodo: PeriodoModel) {
    this.formPeriodo = new FormGroup({
      codigo: new FormControl("", [Validators.required]),
      descripcion: new FormControl("", [Validators.required]),
      nivel: new FormControl("", [Validators.required])
    })

    this.isEdit = periodo != null;
    if (periodo != null) {
      this.formPeriodo.get("codigo").setValue(periodo.codigo);
      this.formPeriodo.get("descripcion").setValue(periodo.descripcion);
      this.formPeriodo.get("nivel").setValue(periodo.nivel);
    }
  }

  listarPeriodos() {
    this.listadoPeriodos = [];
    if (this.searchPagination == null) {
      this.searchPagination = {
        page: this.page,
        records: this.pageSize,
        seek: this.buscador,
      }
    } else {
      this.searchPagination = null
      this.listarPeriodos();
    }
    this.mantenedorService.paginationPeriodos(this.searchPagination).subscribe(result => {
      this.listadoPeriodos = result.content;
      this.totalElements = result.totalElements;
    })
  }

  filtrarPorCodigo(valor: string) {
    this.buscador = valor;
    this.listarPeriodos();
  }

  iniciarEdicionPeriodo(periodo: PeriodoModel) {
    this.initFormPeriodo(periodo);
    this.isModoEdicion = true;
  }


  saveUpdatePeriodo() {
    if (!this.formPeriodo.valid) {
      this.toastr.error("El formulario no es valido.");
    } else {
      this.blockUI.start("Guardando cambios...");
      let periodo = new PeriodoModel();

      periodo.codigo = this.formPeriodo.get("codigo").value.toUpperCase();
      periodo.descripcion = this.formPeriodo.get("descripcion").value;

      if (this.isEdit) {
        this.mantenedorService.updatePeriodo(periodo).subscribe(result => {
          this.volver();
          this.listarPeriodos();
          this.utilAlert.infoSwal("Periodo editado exitosamente.");
        }, (_error) => {
          this.toastr.error(_error?.error?.message);
        })
      } else {
        this.mantenedorService.savePeriodo(periodo).subscribe(result => {
          this.volver();
          this.listarPeriodos();
          this.utilAlert.infoSwal("Periodo creado exitosamente.");
        }, (_error) => {
          this.toastr.error(_error?.error?.message);
        })
      }
      this.blockUI.stop();
    }
  }

  volver() {
    this.isModoEdicion = false;
  }

}
